import React from 'react'
import './topbar.css'

import oskiyu from '../../assets/oskiyu-white.png'
import { NavLink } from 'react-router-dom'

const Topbar = () => {
    return (
      <div>
        <NavLink to="/"><img src={oskiyu} alt="oskiyu" className='image'></img></NavLink>
        <div className='titleBar'></div>
      </div>
    )
  }
  
  export default Topbar
  