import React from 'react'
import { BrowserRouter, Routes, Route } from 'react-router-dom'

import OSKengine from './components/OSKengine/OSkengine'
import MainPage, { GetStaticPosts } from './components/MainPage/Main'
import Topbar from './components/topbar/Topbar'

const App = () => {
  return (
    <BrowserRouter>

    <main>
      <Topbar/>
      <Routes>
        <Route path='/' element={<MainPage />}></Route>
        <Route path='/OSKengine' element={<OSKengine/>}></Route>
      </Routes>
    </main>
    </BrowserRouter>
  )
}

export default App
