import React from 'react'
import './header.css'

import Cta from './Cta' 
import HeaderSocials from './HeaderSocials'

import OSKcard from '../../../assets/card.png'

const Header = () => {
  return (
    <header>
      <div className='container header__container'>
        
          <h5>oskiyu's</h5>
          <h1>OSKengine</h1>

          <div className="bluebar__top"></div>
          <div className="me">
              <div className='me__content'><img src={OSKcard}></img></div>
          </div>
          
          <Cta></Cta>
          <HeaderSocials></HeaderSocials>

          <h2 className='subtitle'>A game engine for the future</h2>

      </div>
    </header>
  )
}

export default Header
